
.App {
  height: 100%;
}

#wrapper{
  display: flex;
  flex-direction:column;
  text-align: center;
  height: 100%;
  background-color: #9fa4ba;
  overflow: auto;
}

h1 {
  font-size: 3em;
  margin-bottom: 0;
}

#menuP {
  font-size: 1.1em;
}

.menubuttons {
  margin-top: 2em;
}

.menubuttons a{
  display: block;
  margin: 1em;
  margin-left: auto;
  margin-right: auto;
  padding: 1.35em 1.1em;
  max-width: 15em;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 800;
  cursor: pointer;
  background: #fffce1;
  border-top-left-radius: 20px 50px;
  border-top-right-radius: 20px 50px;
  border-bottom-right-radius: 20px 50px;
  border-bottom-left-radius: 20px 50px;
}

.menubuttons a:hover{
  background: #c94e50;
  color: #ffffff;
}